<template>
	<section>
		<div class="inner">
	      <div class="crumb">
			当前位置：<a href="">首页</a><em>&gt;</em><span>帮助中心</span>
		  </div>
	    </div>
		 <div class="inner">
	       <div class="cjwt clearfix">
	          <div class="cjwt-l">
	             <div id="firstpane" class="menu_list">
	             	<div v-for="(item,index) in data" class="helpNav">
	             		<p class="menu_head" @click="getIndex(index)" :class="index==num ? 'current11' : ''">{{item.channelName}}</p>
					    <div class="menu_body" :class="index==num ? 'currentActive' : ''" >
					      <a href="javascript:;" v-for="(itemm,index) in item.infoList" @click="getActive(itemm.uuid,index)" :title="itemm.title" :class="index == tabnum ?'helpfontcolor':''">{{itemm.title  | truncate(11)}}</a>
					    </div>
	             	</div>
				</div>
	        </div>
	          <div class="htmltext" v-html="content.textHtml">
	            
	          </div>
	       </div>
   		</div>
	</section>
</template>


<script>
	import { helpData,listDataParticulars } from '../../api/info';
	
	export default{
		data(){
			return{
				data:[],
				content:'',
				num:0,
				uuid:'',
				tabnum:0
			}
		},
		methods:{
			contentDate(){
				helpData({channelMDesc:'HELP'}).then(res=>{
					
					var dataArr = [];
					
					res.datas.forEach(o =>{
						dataArr.push(o)
					})
					this.data=res.datas
					var uuid = dataArr[this.num].infoList[0].uuid
					var data ={
	                        "infoUuid":uuid
					 	}
					listDataParticulars(data).then(res =>{
						this.content=res.datas
					})
				})
			},
			getActive(id,ind){
				this.tabnum = ind
				var data ={
                        "infoUuid":id
				 	}
				listDataParticulars(data).then(res =>{
					this.content=res.datas
				})
			},
			getIndex(index){
				this.num = index
				this.tabnum = 0
				helpData({channelMDesc:'HELP'}).then(res=>{
					
					var dataArr = [];
					
					res.datas.forEach(o =>{
						dataArr.push(o)
					})
					this.data=dataArr
					var uuid = dataArr[this.num].infoList[0].uuid
					var data ={
	                        "infoUuid":uuid
					 	}
					listDataParticulars(data).then(res =>{
						this.content=res.datas
					})
				})
			},
			contentData(){
				
				if(this.$own.getHref('id')){	
					var name = decodeURIComponent(this.$own.getHref('id'));
				}else{
					var name = window.localStorage.getItem('helpPara');
				}
				if(name=='账号问题'){
					this.num = 0
				}else if(name=='原油交易流程'){
					this.num = 1
				}else if(name =='订单执行'){
					this.num = 2
				}else if(name == '租船交易流程'){
					this.num = 3
				}else if(name == '炼厂采购优化'){
					this.num = 4
				}
				this.contentDate()
			}
		},
		watch:{
			'$route' (to, from) {
				if(to.path == from.path){
					var name = this.$route.query.id
					this.contentData(name)
				}
				
			}
		},
		created(){
			
		},
		mounted(){
			this.contentData()
//			this.initData()
			this.contentDate()
		}
	}
</script>


<style>
	.currentActive{
		display: block !important;
	}
	.menu_body{
		display: none;
	}
	.helpfontcolor{
		color: #0066CC !important;
	}
	.htmltext{
		width: calc(100% - 280px);
	    float: right;
	    padding: 20px;
	    max-height: 710px;overflow-y: auto;
	}
</style>


